<script>
import mixin from "@/mixins/component.js";
import mixinPage from "@/mixins/page.js";

export default {
  mixins: [mixin, mixinPage],
  components: {},
  props: {
    obj: {
      type: Object,
      default: function () {
        return {};
      },
    },
  },
  data() {
    return {
      praiseLen: this.obj["praise_len"],
      imgList: [
        {
          title: "封面",
          name: "cover",
          type: "图片",
        },
      ],
      itemList: [
        {
          title: "食物名称",
          name: "food_name",
          type: "文本",
        },
        {
          title: "食物分类",
          name: "food_classification",
          type: "下寻",
        },
        {
          title: "食用禁忌",
          name: "taboo",
          type: "文本",
        },
        {
          title: "能量",
          name: "energy",
          type: "文本",
        },
        {
          title: "适用人群",
          name: "intended_for",
          type: "文本",
        },
      ],
      richList: [
        {
          title: "食物介绍",
          name: "food_introduction",
          type: "编辑",
        },
      ],
    };
  },
  computed: {
    praiseLen() {
      return this.obj.praise_len || 0; // 如果 obj.praise_len 不存在，返回 0 或其他默认值
    },
  },
  methods: {
    get_user_info(name, id) {
      var obj = {
        nickname: name, // 使用传入的name作为nickname
        username: "user_" + id, // 使用传入的id构造一个username
      };
      var ret = "";
      if (obj) {
        ret = obj.nickname + "-" + obj.username;

        // if(obj.nickname){
        // 	ret = obj.nickname;
        // }
        // else{
        // 	ret = obj.username;
        // }
      }
      return ret;
    },
    /**
     * 改变点赞数
     */
    change_praise(obj) {
      var user_id = this.user.user_id;

      var query = {
        source_table: "food_information",
        source_field: "food_information_id",
        source_id: this.obj["food_information_id"],
        user_id,
      };

      var _this = this;

      // 点赞状态
      if (this.state_praise) {
        this.state_praise = false;
        this.$get("~/api/praise/del?", query, (res) => {
          if (res.result) {
            _this.praiseLen = parseInt(_this.praiseLen) - 1;
            var praise_len = _this.praiseLen;
            this.$post(
              "~/api/food_information/set?food_information_id=" +
                _this.obj["food_information_id"],
              {
                praise_len,
              },
              (res) => {
                if (res.result) {
                  console.log("添加点赞数状态：", res.result);
                } else if (res.error) {
                  console.error(res.error);
                }
              }
            );
            // this.$toast("取消点赞");
            this.$message.success("取消点赞");
          } else if (res.error) {
            this.$toast(res.error.message);
            console.error(res.error);
          }
        });
      } else {
        this.state_praise = true;
        this.$post("~/api/praise/add?", query, (res) => {
          if (res.result) {
            _this.praiseLen = parseInt(_this.praiseLen) + 1;
            var praise_len = _this.praiseLen;
            this.$post(
              "~/api/food_information/set?food_information_id=" +
                _this.obj["food_information_id"],
              {
                praise_len,
              },
              (res) => {
                if (res.result) {
                  console.log("添加点赞数状态：", res.result);
                } else if (res.error) {
                  console.error(res.error);
                }
              }
            );
            // this.$toast("点赞成功");
            this.$message.success("点赞成功");
          } else if (res.error) {
            this.$toast(res.error.message);
            console.error(res.error);
          }
        });
      }
    },
    /**
     * 获取点赞
     */
    get_praise() {
      var user_id = this.$store.state.user.user_id;
      this.$get(
        "~/api/praise/count?",
        {
          source_table: "food_information",
          source_field: "food_information_id",
          source_id: this.obj.food_information_id,
          user_id,
        },
        (res) => {
          if (res.result || res.result === 0) {
            console.log("sadsfasf" + JSON.stringify(res));
            this.state_praise = res.result ? true : false;
            console.log("点赞状态：", this.state_praise);
          } else if (res.error) {
            this.$toast(res.error.message);
            console.error(res.error);
          }
        }
      );
    },

    /**
     * 改变收藏状态
     */
    change_collect() {
      var user_id = this.user.user_id;

      var query = {
        source_table: "food_information",
        source_field: "food_information_id",
        source_id: this.obj["food_information_id"],
        user_id,
      };

      // 收藏状态
      if (this.state_collect) {
        this.state_collect = false;
        this.$get("~/api/collect/del?", query, (res) => {
          if (res.result) {
            // this.$toast("取消收藏");
            this.$message.success("取消收藏");
          } else if (res.error) {
            this.$toast(res.error.message);
            console.error(res.error);
          }
        });
      } else {
        this.state_collect = true;
        query.title = this.obj.food_name;
        query.img = this.obj.cover;
        this.$post("~/api/collect/add?", query, (res) => {
          if (res.result) {
            // this.$toast("收藏成功");
            this.$message.success("收藏成功");
          } else if (res.error) {
            this.$toast(res.error.message);
            console.error(res.error);
          }
        });
      }
    },
    check_collect() {
      var user_id = this.$store.state.user.user_id;
      var food_information_id = this.obj.food_information_id;
      this.$get(
        "~/api/collect/count?",
        {
          user_id,
          source_table: "food_information",
          source_field: "food_information_id",
          source_id: food_information_id,
        },
        (res) => {
          this.state_collect = res.result;
        }
      );
    },
  },
  created() {
    setTimeout(() => {
      this.get_praise();
      this.check_collect();
    }, 1000);
  },
  watch: {
    obj: {
      deep: true,
      handler() {
        this.praiseLen = this.obj["praise_len"];
      },
    },
  },
};
</script>

